.skills-info-card {
    min-height: 16rem;
    border-radius: 0.65rem;
    border: 1.5px solid #B415FF;
    background: rgb(22, 22, 22);
    backdrop-filter: blur(1rem);
    margin-bottom: 2rem;

}

.skills-info-card h6 {
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.8rem 2rem;
    text-align: center;
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-bottom: 1.5px solid #B415FF;
}

.skills-info-content {
    padding: 2rem;


}

.skill-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.skill-info p {
    font-size: 1rem;
    font-weight: 500;
}

.skill-info .percentage {
    color: #dd8cfa;
}

.skill-progress-bg {
    width: 100%;
    height: 0.5rem;
    background: #9041f0;
    border-radius: 0.5rem;
    margin: 1rem 0;
    overflow: hidden;
}

.skill-progress {
    width: 0%;
    height: 0.5rem;
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);
    border-radius: 0.5rem;
    transition: all 0.5s ease-in-out;
}