.skills-container {
    margin: 4rem;

    position: relative;
}



.skills-container h5 {
    font-size: 2.0rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 600;
    margin-bottom: 3.5rem;
}

.skills-content {
    display: flex;
    align-items: flex-start;

    font-size: larger;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    gap: 3rem;
}

.skills {
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 4rem;
}

.skills-info {
    flex: 1;
}

@media(max-width:1028px) {
    .skills {
        padding-left: 1rem;
    }

    .skills,
    .skills-content {
        grid-gap: 2rem;
    }
}

@media(max-width:768px) {
    .skills-content {
        flex-direction: column-reverse;
        gap: 3rem;
    }

    .skills,
    .skills-info {
        width: 100%;
        /* margin-bottom: 4rem; */
    }

    .skills-container h5 {
        font-size: 1.8rem;
        margin-bottom: 2rem;
        text-align: center;
    }
}

@media(max-width:600px) {
    .skills-container {
        padding: 0;
    }
}