.hero {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px
}

.hero img {
    margin-top: 120px;
    height: 380px;
    width: 350px;
    border-radius: 9000px;
}

.hero h1 {
    text-align: center;
    width: 100%;
    font-size: 60px;
    font-weight: 600;
}

.hero h1 span {
    background: linear-gradient(270deg, #df8908 10%, #B415FF 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero p {
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}

.hero-action {
    display: flex;
    text-align: center;
    gap: 25px;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 50px;
}

.hero-connect {
    padding: 20px 38px;
    border-radius: 50px;
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);
    cursor: pointer;
}

.hero-resume {
    padding: 20px 38px;
    border-radius: 50px;
    border: 2px solid #FFF;
    cursor: pointer;
    text-decoration: none;
    color: #FFF;
}

.resume {
    text-decoration: none;
    color: #FFF;
}

.hero-connect:hover {
    border: 2px solid #FFF;
}

.hero-resume:hover {
    border: 2px solid #df8908;
}