.project-card {
    background: rgb(22, 22, 21);
    border-radius: 0.65rem;
    border: 1.5px solid #B415FF;
    padding: 1.9rem;
    padding-left: -1.0rem;
    margin: 2rem 4rem;


}

.project-card h6 {
    font-size: 1.08rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.project-link {
    display: inline-block;
    font-size: 0.7rem;
    color: antiquewhite;
    font-weight: 400;
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
}

.project-card ul li {
    list-style: none;
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.project-card ul li::after {
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    background-color: #B415FF;
    border-radius: 0.5rem;
    position: absolute;
    left: -1.1rem;
    top: 0.35rem;
    margin-right: 0.9rem;
}