.skills-card {
    border-radius: 10px;
    border: 1.5px solid #B415FF;
    padding: 2.5rem;
    /* margin-bottom: px; */
    text-align: center;
    background: rgba(22, 17, 47, 0.398);
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
}

.skills-card:hover,
.skills-card.active {
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);

}


.skills-card span {
    font: size 1.3rem;
    font-weight: 500;
}

.skill-icon {
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(22, 22, 22);
    border-radius: 0.65rem;
    border: 1.5px solid #B415FF;
    position: absolute;
    top: -1rem;
    left: -1rem;
}

.skill-icon img {
    width: 2.4rem;
    height: auto;
    object-fit: contain;
}


@media (max-width:1025px) {
    .skills-card {
        padding: 2rem;
    }

    .skills-card span {
        font-size: 1rem;
    }

    .skill-icon {
        width: 3rem;
        height: 3rem;
    }

    .skill-icon img {
        width: 1.8rem;
    }
}

@media(max-width:768px) {

    .skills-card span {
        font-size: 0.9rem;
    }
}