.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* margin-left: 120px; */
    /* margin-right: 30px; */
    margin-top: 10px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

    /* background: rgb(55, 109, 136); */
}

.nav-name {
    font-size: 26px;
    /* margin-left: 25px;
    margin-right: 25px; */
    align-items: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    /* padding: 10px; */
}

.nav-menu {
    display: flex;
    align-items: center;
    font-size: 2rem;
    list-style: none;
    gap: 60px;
    font-size: 18px;

}



.nav-connect {
    padding: 15px 25px;
    border-radius: 50px;
    background: linear-gradient(27deg, #DA7C25 0.36%, #B923E1 102.06%);
    /* margin-left: 5px; */
    font-size: 20px;
    text-align: center;
    cursor: pointer;
    transition: 0.5s;

}

.nav-connect:hover {
    transform: scale(1.05);
}

.menu-btn {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: o.4rem;
    background: linear-gradient(264deg, #df8908 -5.09%, #B415FF 106.28%);
    line-height: 0;
    cursor: pointer;
    transition: all 0.4s ease;
    display: none;
}

.menu-btn:hover {
    color: #B415FF;
    background: rgb(22, 21, 21);
    border: 1px solid #B415FF;
}

.anchor-link {
    text-decoration: none;
    color: white;
}

@media(max-width:425px) {
    .nav-name {
        width: 10rem;
        height: auto;
    }
}


@media(max-width:925px) {
    .menu-btn {
        display: block;
        color: white;
    }

    .nav-menu,
    .nav-connect {
        display: none;
    }

}

@media(max-width:1625px) {
    .navbar {
        padding-top: 0;
        padding-left: 2rem;
    }
}