.mobile-menu {
    width: 100vw;
    height: 100vh;
    /* display: none; */
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999 !important;
    opacity: 0;
    box-shadow: 0px 29px 80px rgba(0, 0, 0, 0.3);
    transition: all o.3s ease;
    transform: translateX(-100vw);
}

.mobile-menu-container {
    width: 60vw;
    height: 100vh;
    background-color: black;
    padding: 2rem;
    cursor: pointer;
}

.mobile-menu.active {
    opacity: 1;
    transform: translateX(0);
}

.Mobilenav-name {
    text-align: center;
    margin-bottom: 3rem;
    font-size: larger;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}

.mobile-menu ul {
    display: flex;
    flex-direction: column;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: large;
    gap: 2rem;
    list-style: none;
    align-items: center;
    /* margin-left: 2rem; */
}

@media(max-width:769px) {
    .mobile-menu {
        display: block;
    }
}